<template>
  <el-container class="heightfull">
    <el-header style="background:#409eff;color:#fff;height:80px;" class="f14">
      <el-row class="heightfull">
        <el-col :span="8" class="df aic heightfull">
          <img src="../../assets/img/logo.png" class="logo" />
        </el-col>
        <el-col :span="16" class="df jcfe aic heightfull">
          <h3 class="mlr20">用户</h3>
          <div class="df ffcw aic jcc heightfull mlr20">
            <span>张三</span>
            <span class="mt10">系统管理员</span>
          </div>
          <div class="mlr20">
            <span class="f14">退出</span>
            <i class="el-icon-arrow-right"></i>
          </div>
        </el-col>
      </el-row>
    </el-header>
    <el-container>
      <el-aside width="auto">
        <!-- <div
          class="w20 df jcc aic"
          @click="isCollapse=!isCollapse"
          style="margin:0 auto;color:#303133;"
        >
          <i :class="isCollapse?'el-icon-arrow-right':'el-icon-arrow-left'"></i>
          <span>{{isCollapse?'展开':'折叠'}}</span>
        </div>-->
        <el-menu :default-active="$route.path" class="heightfull" router>
          <el-menu-item v-for="item in aside" :key="item.link" :index="item.link">
            <i :class="item.icon"></i>
            <span slot="title">{{item.name}}</span>
          </el-menu-item>
        </el-menu>
      </el-aside>
      <el-main class="p20">
        <el-row>
          <el-tag v-for="item in links" :key="item.name" closable >{{item.name}}</el-tag>
        </el-row>
        <router-view />
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  name: "home",
  data() {
    return {
      aside: [
        {
          link: "/home/user/userList",
          icon: "el-icon-user",
          name: "用户列表"
        },
        {
          link: "/home/user/roleList",
          icon: "el-icon-s-flag",
          name: "角色列表"
        },
        {
          link: "/home/user/detailsCollection",
          icon: "el-icon-files",
          name: "组织架构"
        },
        {
          link: "/home/user/logsQuery",
          icon: "el-icon-notebook-1",
          name: "日志查询"
        }
      ],
      links: [] //路由集合
    };
  },
  created() {
    console.log(this.$route)
    this.links.push({
      url:this.$route.path,
      name:this.$route.meta.join(">")
    })
  },
  methods: {}
};
</script>

<style>
.el-aside {
  overflow: hidden;
}
</style>


